<template>
  <div class="wu-infinite-container">
    <mu-appbar title="域名添加">
      <mu-icon-button icon='arrow_back' @click="back" slot="left"/>
    </mu-appbar>
    <div class="pt56 demo-refresh-container">
      <mu-list>
        <template v-for="item in list">
          <mu-list-item :title="item"/>
          <mu-divider/>
        </template>
      </mu-list>
      <mu-infinite-scroll :scroller="scroller" :loading="loading" @load="loadMore"/>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    const list = []
    for (let i = 0; i < 10; i++) {
      list.push('item' + (i + 1))
    }
    return {
      list,
      num: 10,
      loading: false,
      scroller: null
    }
  },
  mounted () {
    this.scroller = this.$el
  },
  methods: {
    back () {
      this.$router.back()
    },
    loadMore () {
      this.loading = true
      setTimeout(() => {
        for (let i = this.num; i < this.num + 10; i++) {
          this.list.push('item' + (i + 1))
        }
        this.num += 10
        this.loading = false
      }, 2000)
    }
  }
}
</script>

<style lang="css">
</style>



